/*
 * App Global CSS
 * ----------------------------------------------------------------------------
 * Put style rules here that you want to apply globally. These styles are for
 * the entire app and not just one component. Additionally, this file can be
 * used as an entry point to import other CSS/Sass files to be included in the
 * output CSS.
 * For more information on global stylesheets, visit the documentation:
 * https://ionicframework.com/docs/layout/global-stylesheets
 */

@import "~swiper/swiper.min.css";
@import "~@ionic/angular/css/ionic-swiper";
/* Core CSS required for Ionic components to work properly */
@import "~@ionic/angular/css/core.css";

/* Basic CSS for apps built with Ionic */
@import "~@ionic/angular/css/normalize.css";
@import "~@ionic/angular/css/structure.css";
@import "~@ionic/angular/css/typography.css";
@import '~@ionic/angular/css/display.css';

/* Optional CSS utils that can be commented out */
@import "~@ionic/angular/css/padding.css";
@import "~@ionic/angular/css/float-elements.css";
@import "~@ionic/angular/css/text-alignment.css";
@import "~@ionic/angular/css/text-transformation.css";
@import "~@ionic/angular/css/flex-utils.css";
.relative{position: relative;}
h1{font-size: 26px !important;white-space: normal; margin: 0; padding: 0;}
h2{font-size: 24px !important;white-space: normal; font-weight: 500; margin: 0; padding: 0;}
h3{font-size: 22px !important;white-space: normal; margin: 0; padding: 0;}
h4{font-size: 18px !important;white-space: normal; margin: 0; padding: 0;}
h5{font-size: 16px !important; color: #0E1127;white-space: normal; margin: 0; padding: 0;}
h6{font-size: 15px !important;white-space: normal;color: #666; font-weight: 400; margin: 0; padding: 0;}
p{padding: 0;margin: 0 auto 10px;font-size: 14px !important;color: #666; font-weight: 400;width: 100%;white-space: normal;}
.font50{font-size: 50px;}
a{color: #000; text-decoration: none;}
a:hover{color: #333; text-decoration: none;}
ion-icon{vertical-align: middle;}
.mb0{margin-bottom: 0px !important;}
.mb10{margin-bottom: 10px !important;}
.mb20{margin-bottom: 20px !important;}
.mb30{margin-bottom: 30px !important;}
.mb50{margin-bottom: 50px !important;}
.mt10{margin-top:10px !important;}
.mt20{margin-top:20px !important;}
.mr-1{margin-right: 10px;}
.ml-1{margin-left: 10px;}
.p-0{padding: 0;}
.m-0{margin: 0;}

ion-header, ion-toolbar{--background: #e42f08; color: #fff;}
ion-header ion-icon{color: #fff !important;}
button, ion-button{background: #e42f08;border-radius: 100px;display: block;width: 100%;padding: 5px 15px;height: 50px;font-size: 16px;color: #fff;font-weight: 500;cursor: pointer !important;}
button:hover, ion-button:hover{background: #c02705;}
button.cancel, ion-button.cancel{background: #585858;border-radius: 100px;display: block;width: 100%;padding: 5px 15px;height: 50px;font-size: 16px;color: #fff;font-weight: 500;cursor: pointer !important;}
button.cancel:hover, ion-button.cancel:hover{background: #333;}

.gray_btn{background: #ccc; font-size: 12px; text-align: center; border-radius: 100px; display: inline-block; padding: 3px 10px; color: #000;}
.red_btn{background: #f00; font-size: 12px; text-align: center; border-radius: 100px; display: inline-block; padding: 3px 6px; color: #fff;}
.green_btn{background: rgb(0, 179, 45); font-size: 12px; text-align: center; border-radius: 100px; display: inline-block; padding: 3px 6px; color: #fff;}
.see_green_btn{background: #00b894; font-size: 12px; text-align: center; border-radius: 100px; display: inline-block; padding: 3px 6px; color: #fff;}
.orange_btn{background: #ffaf00; font-size: 12px; text-align: center; border-radius: 100px; display: inline-block; padding: 3px 6px; color: #fff;}
.danger_btn{background: #ea4c62; font-size: 12px; text-align: center; border-radius: 100px; display: inline-block; padding: 3px 6px; color: #fff;}
.blue_btn{background: #21d6f1; font-size: 12px; text-align: center; border-radius: 100px; display: inline-block; padding: 3px 6px; color: #fff;}
.load_more_btn{background: #f00; font-size: 13px; text-align: center; border-radius: 4px; display: inline-block; padding: 5px 10px; color: #fff;}
.load_more_btn:hover{background: #000; text-decoration: none;}

ion-icon{vertical-align: middle;}
ion-card.card {background-color: #fff;border: 1px solid #eaeaea;border-radius: 10px;text-align: center;padding: 10px;margin: 0;margin-top: 20px;top:10px}
ion-input{input::placeholder {color:rgba(14,17,39,0.4)!important;}}

.text-white{color: #fff;}
.text-yellow{color: #ffaf00;}

#background-content{--background: #f00 !important;}

.d-block{display: block;}

.about_img{max-width: 100%; max-height: 60px;vertical-align: middle;}
.border_bottom_red{border-bottom: 3px solid #e42f08;}
.button_roundad{border-radius: 6px;height: 40px;}